<template>
    <div id="login">
        <div id="login_img">
            <van-image
            round
            width="8rem"
            height="4rem"
            :src="require('../assets/logo.png')" />
        </div>
        <van-form @submit="onSubmit">
          <van-field
              v-model="account"
              name="account"
              left-icon="contact"
              label=""
              placeholder=""
              :rules="[{ required: true, message: '' }]"
          />
          <van-field
              v-model="password"
              type="password"
              name="password"
              left-icon="passed"
              label=""
              placeholder=""
              :rules="[{ required: true, message: '' }]"
          />
          <div style="margin: 16px;">
              <van-button round block color="#7232dd" native-type="submit">
              
              </van-button>
          </div>
        </van-form>
        <van-divider
            :style="{ color: '#1989fa', borderColor: '#1989fa', padding: '10 25px' }">
            
        </van-divider>
        <van-cell title="" is-link to="register" />
    </div>
</template>

<script>
import { Toast, Image, Form, Field, Button, Cell, Divider } from 'vant';


export default {
    data() {
        return {
          account: '',
          password: ''
      };
    },
    // vant
    components: {
        [Button.name]: Button,
        [Toast.name]: Toast,
        [Image.name]: Image,
        [Form.name]: Form,
        [Field.name]: Field,
        [Cell.name]: Cell,
        [Divider.name]: Divider

    },
    methods: {
        router_registered(){
            this.$router.push('register');
        },
        router_home(){
            this.$router.push('/');
        },
        onSubmit(values){
            this.axios.post('/user/login', values)
        .then(resp => {
            if (resp.data.code == 200){
                Toast({
                    message: '',
                    icon: 'like-o',
                });
              // token
              localStorage.setItem('token',resp.data.data.token);
              
              setTimeout(this.router_home, 3000);
            } else if (resp.data.code == -10){
                Toast.fail(resp.data.msg);
            } else if (resp.data.code == -5){
                Toast.fail(resp.data.msg);
                setTimeout(this.router_registered, 3000);
            }
            console.log(resp);
        }).catch(err => {
            Toast.fail('!');
            console.log(err);
        })
        console.log('data', values);
        },
    },
};
</script>

<style scoped>
#login_img {
        margin-top: 20px;
        margin-bottom: 10px;
}
#login {
        text-align: center;
}
</style>