<template>
    <div class="friend">
        
        <form action="/">
          <van-search
            v-model="account"
            shape="round"
            placeholder=""
            @search="onSearch"
          />
        </form>

        <div v-if="friend !=''">
            <div class="tip">
            <span></span>
            </div>
            <div class="item" v-for="item in friend" :key="item.uid">
                <img class="face" :src="imgUrl.imgUrl + item.avatar">
                <div class="des">
                    <div class="nickName"><span>{{item.nick_name}}</span></div>
                    <div class="signature"><span>{{item.summary}}</span></div>
                </div>
                <div class="addbt">
                    <van-button type="success" size="mini" round @click="onAddFriend"></van-button>
                </div>
            </div>
        </div>


        <div class="tip">
        <span></span>
        </div>
        <div class="item" v-for="item in list" :key="item.uid">
            <img class="face" :src="imgUrl.imgUrl + item.avatar">
            <div class="des">
                <div class="nickName"><span>{{item.nick_name}}</span></div>
                <div class="signature"><span>{{item.summary}}</span></div>
            </div>
            <div class="addbt">
                <van-button type="danger" size="mini" round @click="onRefuse(item.request_id)" ></van-button>
                <van-button type="success" size="mini" round @click="onAgree(item.request_id)" ></van-button>
            </div>
        </div>


    </div>

</template>



<script>
import { Toast, Button, Search, Skeleton, Cell, Image as VanImage } from 'vant';
import imgUrl from '@/global';


export default {

    name: 'AddFriend',
    components: {
        [Search.name]: Search,
        [Button.name]: Button,
        [Toast.name]: Toast
    },

    data()  {
      return {
            list: [],
            friend: '',
            account: '',
            imgUrl
        }
    },
    methods: {
    // 
    onSearch (args){
        this.axios.post('/user/search_friend', {'account': args})
        .then(resp => {
            if (resp.data.code == 200){
                this.friend = [resp.data.data];
            } else if (resp.data.code == 100){
                Toast.fail(resp.data.msg);
            } else if (resp.data.code == -5){
                Toast.fail(resp.data.msg);
                // 
                this.redirect_login();
            }
        }).catch(err => {
            Toast.fail('!');
            console.log(err);
        });
        },
    // 
    onAddFriend (){
        this.axios.post('/user/add_friend_request', {'add_user': this.account})
        .then(resp => {
            if (resp.data.code == 200){
                Toast.fail(resp.data.msg);
            } else if (resp.data.code == 100){
                Toast.fail(resp.data.msg);
            } else if (resp.data.code == -5){
                Toast.fail(resp.data.msg);
                // 
                this.redirect_login();
            }
        }).catch(err => {
            Toast.fail('!');
            console.log(err);
        });
    },
    // 

    // 
    onAgree (res_id){
        this.axios.post('/user/confirm_add_request', {
            'request_id': res_id,
            'is_ok': true
        })
        .then(resp => {
            if (resp.data.code == 200){
                Toast.fail(resp.data.msg);
            } else if (resp.data.code == 100){
                Toast.fail(resp.data.msg);
            } else if (resp.data.code == -5){
                Toast.fail(resp.data.msg);
                // 
                this.redirect_login();
            }
        }).catch(err => {
            Toast.fail('!');
            console.log(err);
        });
    },
    // 
    onRefuse (res_id){
        this.axios.post('/user/confirm_add_request', {
            'request_id': res_id,
            'is_ok': false
        })
        .then(resp => {
            if (resp.data.code == 200){
                Toast.fail(resp.data.msg);
            } else if (resp.data.code == 100){
                Toast.fail(resp.data.msg);
            } else if (resp.data.code == -5){
                Toast.fail(resp.data.msg);
                // 
                this.redirect_login();
            }
        }).catch(err => {
            Toast.fail('!');
            console.log(err);
        });
    }
    },
    created() {
        // 
        this.axios.post('user/get_friend_request')
        .then(resp => {
            if (resp.data.code == 200){
                this.list = resp.data.data
            } else if (resp.data.code == -10){
                Toast.fail(resp.data.msg);
            } else if (resp.data.code == -5){
                Toast.fail(resp.data.msg);
                // 
                this.redirect_login();
            }
        }).catch(err => {
            Toast.fail('!');
            console.log(err);
        });
    }
}
</script>


<style>
  .tip{
        height: 20px;
        line-height: 20px;
        font-size: 14px;
        color: gray;
        padding-left: 10px;
      }
      .none{
      	display: none;
      }
      .item{
		  width: 100%;
		  height: 60px;
		  box-sizing: border-box;
		  border-bottom: 1px solid #eee;
		  background-color: #fff;
		  display: flex;
		}
		.face{
		  width: 44px;
		  height: 44px;
		  border-radius: 50%;
		  position: absolute;
		  left: 10px;
		  margin-top: 8px;
		  object-fit: cover;
		}
		.des{
		  height: 60px;
		  display: inline-block;
		  margin-left: 65px;
		  white-space :  nowrap;
		  text-overflow : ellipsis;
		  overflow:hidden;
		  flex: 1;
		}
		.nickName{
		  /*height: 30px;*/
		  margin-top: 10px;
		}
		.nickName span{
		  height: 25px;
		  line-height: 25px;
		  font-size: 16px;
		  white-space :  nowrap;
		  text-overflow : ellipsis;
		  overflow:hidden;
		}
		.signature{
		  /*height: 30px;*/
		  margin-top: -5px;
		}
		.signature span{
		  height: 20px;
		  line-height: 20px;
		  font-size: 12px;
		  color:gray;
		  white-space :  nowrap;
		  text-overflow : ellipsis;
		  overflow:hidden;
		}
		.sel{
		  width: 30px;
		  height: 60px;
		  display: inline-block;
		  /* float: right; */
		  margin-right: 5px;
		}
		.sel img{
		  width: 30px;
		  height: 30px;
		  margin-top: 15px;
		}
        .addbt {
            margin-top: 1rem;
            padding-right: 1rem;
        }

</style>
