<template>
    <div class="news">
        <van-form @submit="onAddNews">
            <van-cell title="" icon="arrow-left" is-link to="/" value="" />

            <van-cell-group>
                <van-field
                    name="title"
                    v-model="title"
                    type="textarea"
                    maxlength="512"
                    placeholder="..."
                    show-word-limit
                />
            </van-cell-group>

            <van-field name="files">
            <template #input>
                <van-uploader v-model="files" :max-size="5000 * 1024" :max-count="6" multiple/>
            </template>
            </van-field>

            <van-field
                v-model="result"
                name="showPicker"
                is-link
                readonly
                placeholder=""
                @click="onConfirm"
            />
            <van-popup v-model:show="showPicker" position="bottom">

                <van-checkbox-group v-model="checked" ref="checkboxGroup">
                    <van-cell-group>
                        <van-cell title="" icon="arrow-left" is-link to="/" value="" />
                        <div class="addbt">
                            <van-button type="danger" size="mini" round @click="checkAll"></van-button>
                            <van-button type="success" size="mini" round @click="toggleAll"></van-button>
                        </div>
                        <div class="tip">
                        <span></span>
                        </div>
                        
                        <div class="item" v-for="(item, index) in friends" :key="item.uid">
                            <img class="face" :src="imgUrl.imgUrl + item.avatar">
                            <div class="des" @click="toggle(index)">
                                <div class="nickName"><span>{{item.nick_name}}</span></div>
                                <div class="signature"><span>{{item.summary}}</span></div>
                            </div>
                            <div class="addbt" name="fgfgf">
                                <van-checkbox
                                checked-color="#7232dd"
                                :name="item.uid"
                                :ref="el => checkboxRefs[index] = el"
                                @click.stop
                                />
                            </div>
                        </div>

                    </van-cell-group>
                </van-checkbox-group>


            </van-popup>

            <div style="margin: 16px;">
                <van-button round block color="#7232dd" native-type="submit">
                
                </van-button>
            </div>
        </van-form>
    </div>
</template>


<script>
import { ref } from 'vue';
import imgUrl from '@/global';
import { Toast, Button, CellGroup , Cell, Form, Field, Uploader, Picker, Popup, Checkbox, CheckboxGroup } from 'vant';


export default {
  name: 'AddNews',
  components: {
    [Button.name]: Button,
    [ref.name]: ref,
    [CellGroup .name]: CellGroup,
    [Cell.name]: Cell,
    [Toast.name]: Toast,
    [Field.name]: Field,
    [Form.name]: Form,
    [Uploader.name]: Uploader,
    [Picker.name]: Picker,
    [Popup.name]: Popup,
    [Checkbox.name]: Checkbox,
    [CheckboxGroup.name]: CheckboxGroup
  },

  data (){
      return {
          files: [],
          title: '',
          result: '',
          showPicker: false,
          checked: [],
          friends: [],
          checkboxRefs: [],
          imgUrl
      }
  },

  methods:{
      onAddNews() {
          //   
          if (this.files.length == 0){
              if (this.title == ''){
                  Toast.fail('!');
              } else{
                let params = { "title": this.title, "friend_view_list":  this.checked};
                this.axios.post('/user/post_news', params)
                    .then(resp => {
                        if (resp.data.code == 200){
                            Toast.fail(resp.data.msg);
                            this.$router.push('/')
                        } else if (resp.data.code == 100){
                            Toast.fail(",");
                        } else if (resp.data.code == -99){
                            Toast.fail(resp.data.msg);
                        } else if (resp.data.code == -5){
                            Toast.fail(resp.data.msg);
                            // 
                            this.$router.push('login');
                        }
                    }).catch(err => {
                        Toast.fail('!');
                        console.log(params);
                    });
             }
          } else{
              let params = { "title": this.title, "files": this.files, "friend_view_list":  this.checked};
                this.axios.post('/user/post_news', params)
                    .then(resp => {
                        if (resp.data.code == 200){
                            Toast.fail(resp.data.msg);
                            this.$router.push('/')
                        } else if (resp.data.code == 100){
                            Toast.fail(",");
                        } else if (resp.data.code == -99){
                            Toast.fail(resp.data.msg);
                        } else if (resp.data.code == -5){
                            Toast.fail(resp.data.msg);
                            // 
                            this.$router.push('login')
                        }
                    }).catch(err => {
                        Toast.fail('!');
                        console.log(params);
                    });
          }
      },
      onConfirm() {
          //   
          this.axios.post('/user/get_friend_list')
            .then(resp => {
                if (resp.data.code == 200){
                    this.friends = resp.data.data
                } else if (resp.data.code == 100){
                    Toast.fail(",");
                } else if (resp.data.code == -5){
                    Toast.fail(resp.data.msg);
                    // 
                    this.$router.push('login');
                }
            }).catch(err => {
                Toast.fail('!');
                console.log(err);
            });
          this.showPicker = true;
      },
      //  
      checkAll() {
          this.$refs.checkboxGroup.toggleAll(true);
     },
     //  
      toggleAll() {
          this.$refs.checkboxGroup.toggleAll();
    },
      toggle(index) {
          this.checkboxRefs[index].toggle();
          console.log(this.checked);
    }
  }
}
</script>

<style>
  .tip{
        height: 20px;
        line-height: 20px;
        font-size: 14px;
        color: gray;
        padding-left: 10px;
      }
      .none{
      	display: none;
      }
      .item{
		  width: 100%;
		  height: 60px;
		  box-sizing: border-box;
		  border-bottom: 1px solid #eee;
		  background-color: #fff;
		  display: flex;
		}
		.face{
		  width: 44px;
		  height: 44px;
		  border-radius: 50%;
		  position: absolute;
		  left: 10px;
		  margin-top: 8px;
		  object-fit: cover;
		}
		.des{
		  height: 60px;
		  display: inline-block;
		  margin-left: 65px;
		  white-space :  nowrap;
		  text-overflow : ellipsis;
		  overflow:hidden;
		  flex: 1;
		}
		.nickName{
		  /*height: 30px;*/
		  margin-top: 10px;
		}
		.nickName span{
		  height: 25px;
		  line-height: 25px;
		  font-size: 16px;
		  white-space :  nowrap;
		  text-overflow : ellipsis;
		  overflow:hidden;
		}
		.signature{
		  /*height: 30px;*/
		  margin-top: -5px;
		}
		.signature span{
		  height: 20px;
		  line-height: 20px;
		  font-size: 12px;
		  color:gray;
		  white-space :  nowrap;
		  text-overflow : ellipsis;
		  overflow:hidden;
		}
		.sel{
		  width: 30px;
		  height: 60px;
		  display: inline-block;
		  /* float: right; */
		  margin-right: 5px;
		}
		.sel img{
		  width: 30px;
		  height: 30px;
		  margin-top: 15px;
		}
        .addbt {
            display: flex;
            margin-top: 0.5rem;
            margin-bottom: 0.3rem;
            padding-right: 1rem;
            margin-left: 0.7rem;
        }

</style>
